/**
 * @Internal dependencies
 */
import hasDuplicateValueInArrays from './has-duplicate-value-in-arrays';

/**
 * 
 *
 * @param {string} nowClass
 * @param {Array}  classNames
 * @return {boolean} 
 */
export default function hasAnyClass(nowClass, classNames) {
	if (!nowClass) return false;

	// 
	const nowClasses = nowClass.split(' ');

	// 
	return hasDuplicateValueInArrays(nowClasses, classNames);
}
