/**
 * @Internal dependencies
 */
import hasClass from './has-class';

/**
 * @others dependencies
 */
import classnames from 'classnames/dedupe';

/**
 * class
 *
 * @param {string} nowClass
 * @param {string} targetClass
 * @param {Array}  classGroup
 * @param {string} commonClass
 * @return {boolean} 
 */
export default function setClass(nowClass, targetClass, classGroup, commonClass = '') {
	// 
	if (hasClass(nowClass, targetClass)) {
		return nowClass;
	}

	let newClass;

	if (classGroup) {
		// classnames  false 
		const classInit = {};
		classGroup.map((classname) => {
			classInit[classname] = false;
		});
		if (commonClass) {
			classInit[commonClass] = false;
		}

		// 
		newClass = classnames(nowClass, classInit);

		if (targetClass) {
			newClass = classnames(newClass, commonClass, targetClass);
		}
	} else if (targetClass) {
		// 
		newClass = classnames(nowClass, targetClass);
	}
	return newClass;
}
