/**
 * isNewTab  rel
 *
 * @param {boolean} isNewTab
 * @param {string}  rel
 * @return {string} rel
 */
export default function getNewLinkRel(isNewTab, rel) {
	let newRel = rel || '';
	if (isNewTab) {
		// noopener 
		if (-1 === newRel.indexOf('noopener')) {
			newRel += ' noopener';
		}
		// noreferrer 
		if (-1 === newRel.indexOf('noreferrer')) {
			newRel += ' noreferrer';
		}
	} else {
		// noopener / noreferrer 
		newRel = newRel.replace('noopener', '');
		newRel = newRel.replace('noreferrer', '');
	}

	return newRel.trim();
}
